.content-editor {
  position: fixed;
  padding: 0 10px;
  height: 50px;
  line-height: 50px;
  width: 100%;
  z-index: 10000;
  top: 0;
}
.content-editable header {
  margin-top: 50px;
}
.content-editable .cke_button_label.cke_button__inlinesave_label {
  display: inline;
}
.content-editable .content-editable-container {
  position: relative;
  outline: 0;
  border: 1px solid #cecece;
}
.content-editable .content-editable-container [data-editable=true] {
  position: relative;
}
.content-editable .content-editable-container .button-toolbar {
  display: none;
  position: absolute;
  text-align: center;
  top: 100%;
  margin-left: -1px;
  margin-right: -1px;
  width: calc(100% + 2px);
  background: white;
  z-index: 99991;
  border: 1px solid #cecece;
  border-top: none;
}
.content-editable .content-editable-container .button-toolbar button {
  margin: 0 5px;
}
.content-editable .content-editable-container .upload-btn-wrapper {
  position: absolute;
  text-align: center;
  opacity: 0;
  width: 100px;
  height: 40px;
  left: 50%;
  margin-left: -50px;
  top: 50%;
  margin-top: -20px;
  overflow: hidden;
  display: inline-block;
}
.content-editable .content-editable-container .upload-btn-wrapper input[type=file] {
  font-size: 100px;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}
.content-editable .content-editable-container.active {
  border-bottom-color: transparent;
}
.content-editable .content-editable-container.active .button-toolbar {
  display: block;
}
.content-editable .content-editable-container:hover .upload-btn-wrapper {
  opacity: 1;
}
.content-editable .content-editable-section {
  position: relative;
}
.content-editable .content-editable-section .upper-button-toolbar {
  display: none;
  position: absolute;
  text-align: center;
  top: 0;
  margin-right: 0;
  z-index: 999;
}
.content-editable .content-editable-section.hidden {
  display: none;
}
.content-editable .content-editable-section:hover .upper-button-toolbar {
  display: block;
}
.content-editable .hidden-item-container .hidden-btn {
  display: none;
}
.content-editable .hidden-item-container .hidden-btn.block {
  display: block;
}
.content-editable .hidden-item-container .hidden-section {
  min-width: 400px;
}
.content-editable .hidden-item-container .hidden-section .hidden-item {
  margin-left: 10px;
}
.content-editable .content-hidden {
  opacity: 0.5;
}
#linked-modal {
  margin-top: 50px;
}
.content-editable-link {
  position: absolute;
  top: 0;
  right: 0;
}
